body {
  position: relative;
  min-width: 375px;
  margin: 0;
  background: #fff;
  font-size: 14px;
  line-height: 1.5;
  text-size-adjust: none;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

*,
::before,
::after {
  box-sizing: border-box;
}

.blind {
  position: absolute;
  width: 1px;
  height: 1px;
  font-size: 1px;
  overflow: hidden;
  opacity: 0;
}

.ko-KR {
  font-family: 'Noto Sans KR', 'Apple SD Gothic Neo', NanumBarunGothic, NanumGothic, 'Malgun Gothic', dotum, sans-serif;
  word-break: keep-all;
}

img {
  max-width: 100%;
  height: auto;
}

ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

.portfolio__gnb {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  padding-left: 8px;
  padding-right: 8px;
  border-top: 10px solid #fff;
  border-bottom: 10px solid #fff;
}

.portfolio__home {
  height: 100%;
  padding-left: 8px;
  padding-right: 8px;
}

.portfolio__home .content__title {
  text-align: right;
}

.portfolio__home .content__title h2 {
  margin: 0;
  font-family: "Black Han Sans", sans-serif;
  font-size: 30px;
  color: #036635;
}

.portfolio__home .content__title p {
  font-size: 16px;
  color: #000;
}

.portfolio__home .content__slide {
  padding-top: 15px;
  padding-bottom: 15px;
}

.portfolio__home .content__slide .swiper-container {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.portfolio__home .content__slide .swiper-container .swiper-slide {
  overflow: hidden;
  padding: 0 20px;
  border-radius: 15px;
  background: #11998e;
  background: linear-gradient(to bottom, #38ef7d, #11998e);
}

.portfolio__home .content__slide .swiper-container .innerSlide {
  overflow: hidden;
  position: relative;
  width: 100%;
  padding-top: 20px;
  border-radius: 12px;
}

.portfolio__home .content__slide .swiper-container .innerSlide .thumb img {
  width: 100%;
  height: 100%;
  vertical-align: top;
}

.portfolio__home .content__slide .swiper-container .innerSlide .swiper-slide {
  padding: 0;
}

.portfolio__home .content__slide .swiper-container .innerSlide .swiper-button-next,
.portfolio__home .content__slide .swiper-container .innerSlide .swiper-button-prev {
  color: #fff;
  backface-visibility: hidden;
  transform: translateZ(0);
  transform: translate3d(0, 0, 0);
}

.portfolio__home .content__slide .swiper-container .innerSlide--box {
  padding: 10px 0;
  min-height: 175px;
}

.portfolio__home .content__slide .swiper-container .innerSlide--box a {
  text-decoration: underline;
}

.portfolio__home .content__slide .swiper-container .innerSlide--content {
  min-height: 120px;
}

.portfolio__home .content__slide .swiper-container .innerSlide--content h3 {
  margin: 0 0 5px 0;
  color: #fff;
  font-size: 16px;
}

.portfolio__home .content__slide .swiper-container .innerSlide--content dl {
  margin: 0;
}

.portfolio__home .content__slide .swiper-container .innerSlide--content dl dt, .portfolio__home .content__slide .swiper-container .innerSlide--content dl dd {
  color: #fff;
}

.portfolio__home .content__slide .swiper-container .innerSlide--content dl dt {
  min-width: 70px;
  float: left;
}

.portfolio__home .content__slide .swiper-container .innerSlide--content dl dd {
  overflow: hidden;
  white-space: nowrap;
  text-align: right;
  text-overflow: ellipsis;
}

.portfolio__home .content__slide .swiper-container .innerSlide--content dl dd ul li {
  overflow: hidden;
  white-space: nowrap;
  text-align: right;
  text-overflow: ellipsis;
}

.portfolio__home .content__slide .swiper-container .innerSlide--button {
  padding-top: 10px;
  text-align: center;
}

.portfolio__home .content__slide .swiper-container .innerSlide--button .more__button {
  display: block;
  padding: 10px;
  background: #fff;
  border: 0;
  border-radius: 12px;
  appearance: none;
  text-decoration: none;
}

.portfolio__home .pFaccordion__faq {
  position: relative;
  margin-bottom: 20px;
}

.portfolio__home .pFaccordion__contentWrap {
  padding: 20px;
}

.portfolio__home .pFaccordion__title {
  position: relative;
  padding: 10px 10px 10px 45px;
  background: #f0f2f5;
  font-size: 15px;
  font-weight: bold;
  color: #333;
}

.portfolio__home .pFaccordion__title h3 {
  margin: 0;
}

.portfolio__home .pFaccordion__title:before {
  position: absolute;
  top: 50%;
  left: 12px;
  margin-top: -10px;
  background-image: url(../img/plus-circle.svg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 20px;
  display: block;
  width: 20px;
  height: 20px;
  content: '';
}

.portfolio__home .pFaccordion__content {
  border-left: 1px solid #f0f2f5;
  border-right: 1px solid #f0f2f5;
}

.portfolio__home .pFaccordion__section:first-of-type .pFaccordion__title {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.portfolio__home .pFaccordion__section:last-of-type .pFaccordion__content {
  border-bottom: 1px solid #f0f2f5;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.portfolio__home .pFaccordion__section--active .pFaccordion__title:before {
  background-image: url(../img/minus-circle.svg);
}

.gnb {
  margin-left: 20px;
}

.gnb__logo {
  overflow: hidden;
  width: 48px;
  height: 48px;
  border-radius: 100%;
  background-color: yellow;
}

.gnb__logo img {
  width: 100%;
  height: 100%;
}

.gnb__list {
  display: flex;
}

.gnb__item {
  margin-right: 8px;
}

.gnb__link {
  display: inline-block;
  position: relative;
  padding: 0 8px;
  font-size: 14px;
  font-weight: 500;
  color: #000;
  line-height: 48px;
  text-align: center;
}

.drawer {
  margin-left: auto;
}

.drawer .nav__button {
  overflow: hidden;
  position: relative;
  width: 48px;
  height: 48px;
  padding: 0 16px;
  border: 0;
  background: transparent;
  color: transparent;
  white-space: nowrap;
  text-align: left;
  line-height: 48px;
  -webkit-tap-highlight-color: initial;
}

.drawer .nav__button::after {
  content: '';
  position: absolute;
  height: 2px;
  top: 18px;
  left: 16px;
  right: 16px;
  vertical-align: top;
  background: #000;
  box-shadow: 0 5px #000, 0 10px #000;
}

.drawer__profile {
  display: none;
  position: absolute;
  top: 64px;
  right: 16px;
  width: 280px;
  transform-origin: 100% 0;
  border: 1px solid #dbdee3;
  background-color: #fff;
}

.drawer__closeBtn {
  overflow: hidden;
  position: relative;
  float: right;
  width: 48px;
  height: 48px;
  padding-top: 48px;
  color: transparent;
  border: 0;
  background-color: transparent;
}

.drawer__closeBtn:before, .drawer__closeBtn:after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 18px;
  height: 2px;
  background: #000;
}

.drawer__closeBtn:before {
  transform: rotate(45deg);
}

.drawer__closeBtn:after {
  transform: rotate(-45deg);
}

.drawer.active ~ .drawer__profile {
  display: block;
}
